<template>
  <div>
    <a-input-search
      v-model:value="searchValue"
      placeholder=""
      style="width: 200px"
      @search="onSearch"
    />
  </div>
    <a-table :columns="columns" rowKey="order_id" :data-source="ordersList" bordered :pagination="false">
      <template #handler>
        <a-button type="primary" @click="editCityVisible = !editCityVisible"></a-button>
        <a-button type="primary" danger @click="chaKanWuLiu"></a-button>
      </template>
      <template #time="{text}">
        {{dateFormat(text)}}
      </template>
      <template #isPay="{text}">
        <a-tag v-if="text=='0'" color="red"></a-tag>
        <a-tag v-else color="green"></a-tag>
      </template>
    </a-table>
    <!--  -->
           <a-config-provider :locale="locale">
              <a-pagination @change="pageChangeHandler" @showSizeChange="pageChangeHandler" :total="ordersTotal" :defaultPageSize="5" :pageSizeOptions="['1','2','5','10']" show-size-changer show-quick-jumper />
           </a-config-provider>
    <a-modal v-model:visible="editCityVisible" title="" @ok="handleOk">
      <p>
         <!-- v-model:value="value"
         :display-render="displayRender" -->

        :
        <a-cascader
          :options="options"
          expand-trigger="hover"
          placeholder=""
        />
      </p>
      <p>
        <span>:  </span><a-input style="width:80%"></a-input>
      </p>
    </a-modal>
    <a-modal v-model:visible="viewLogistics" :footer="null" title="">
       <a-timeline>
          <a-timeline-item v-for="(item,index) in KuaiDiList" :key="index" :color="index == 0 ? 'green' : 'blue'">
            <p>{{item.context}}</p>
            <p>{{item.time}}</p>
          </a-timeline-item>

      </a-timeline>
    </a-modal>
</template>

<script>
import { getCurrentInstance, onMounted } from 'vue'
import zhCN from 'ant-design-vue/es/locale/zh_CN'
import { ref } from '@vue/reactivity'
import { message } from 'ant-design-vue'
import cityDate from '../assets/js/citydata'
import { dateFormate } from '../assets/js/dateformate'
export default {
  setup () {
    const dateFormat = dateFormate
    const { proxy } = getCurrentInstance()
    const columns = [
      {
        title: 'ID',
        dataIndex: 'order_id'
      },
      {
        title: '',
        dataIndex: 'order_number'
      },
      {
        title: '',
        dataIndex: 'pay_status',
        slots: { customRender: 'isPay' }
      },
      {
        title: '',
        dataIndex: 'is_send'
      },
      {
        title: '',
        dataIndex: 'create_time',
        slots: {
          customRender: 'time'
        }
      },
      {
        title: '',
        slots: { customRender: 'handler' }
      }
    ]
    // 
    const queryParams = {
      query: '',
      pagenum: 1,
      pagesize: 5
    }
    //  
    const searchValue = ref()
    // 
    const onSearch = value => {
      queryParams.query = value
      queryOrdersList()
    }
    // 
    const ordersTotal = ref()
    // 
    const pageChangeHandler = (pagenum, pagesize) => {
      queryParams.pagenum = pagenum
      queryParams.pagesize = pagesize
      queryOrdersList()
    }
    //  
    const options = cityDate
    //  
    const editCityVisible = ref(false)
    //  
    const viewLogistics = ref(false)
    //  
    const ordersList = ref()

    const queryOrdersList = async () => {
      const { meta, data } = await proxy.$http.get('orders', { params: queryParams })
      if (meta.status != 200) return message.error(meta.msg)
      ordersList.value = data.goods
      ordersTotal.value = data.total
    }
    onMounted(() => {
      queryOrdersList()
    })

    const KuaiDiList = ref()
    // 
    const chaKanWuLiu = async () => {
      const { meta, data } = await proxy.$http.get('kuaidi/' + 1106975712662)
      if (meta.status != 200) return message.error(meta.msg)
      KuaiDiList.value = data
      viewLogistics.value = true
    }
    // 
    const handleOk = () => {
      editCityVisible.value = false
      message.info('! ! ! ')
    }
    return {
      searchValue,
      dateFormat,
      chaKanWuLiu,
      options,
      handleOk,
      onSearch,
      ordersTotal,
      pageChangeHandler,
      viewLogistics,
      queryOrdersList,
      KuaiDiList,
      locale: zhCN,
      ordersList,
      editCityVisible,
      columns

    }
  }
}
</script>

<style scoped>

</style>
